.form_change {
  width: 400px;
  min-height: 300px;
  background-color: #ffffff;
  margin: auto auto;
  padding: 20px;

  .change_password {
    .ant-form-item {
      margin-bottom: 10px;
      font-size: 14px;
    }
  
    .txt_label {
      font-weight: 400;
      font-size: 14px;
    }
  
    .txt_input {
      margin-bottom: 10px;
      font-weight: 400;
      font-size: 14px !important;
    }
  
    .btn_add {
      background: #0064e6;
      border-radius: 5px;
      height: 40px;
    }
    
    .btn_cancel {
      margin-right: 15px;
      border-radius: 5px;
      height: 40px;
    }
  }
}